import React, { Component } from 'react';
import { Avatar } from 'antd';
import './index.scss';
import { connect } from 'react-redux';
import RankList from '../../../components/ranking/index';
import Liver from '../../../components/video/index';
import AnchorList from '../../../components/anchor_list/index';
import CarouselBanner from '../../../components/carousel/index';
import Tool from '../../../components/tool/index';
import { setRoom, attention } from '../../actions/index/index';
import { anchorRank, tycoonRank } from '../../../asset/js/api';

class Index extends Component {
  state = {
    loading: true,
    activeIndex: 1,
    list: [
      { index: 1 },
      { index: 2 },
      { index: 3 },
      { index: 4 },
    ],
    data: [],
    recommendHeader: {
      badge: 'recommend',
      title: '热门推荐',
    },
    attentionHeader: {
      badge: 'follow',
      title: '我的关注',
    },
    ranklist: [
      {
        nickname: 'test1', level: 1, img: '', money: 99999,
      },
      {
        nickname: 'test2', level: 2, img: '', money: 2222,
      },
      {
        nickname: 'test3', level: 3, img: '', money: 33333,
      },
      {
        nickname: 'test4', level: 4, img: '', money: 55555,
      },
      {
        nickname: 'test5', level: 5, img: '', money: 8888,
      },
    ],
  };

  componentDidMount = () => {
    const { rommList, myattention } = this.props;
    myattention();
    rommList();
  }

  changeIndex= (index) => {
    this.setState({
      activeIndex: index + 1,
    });
    console.log(this.refs);
    this.refs.liver.destory();
  }

  changeSrc = () => {
    console.log('a');
  }

  render() {
    const {
      list, activeIndex, recommendHeader, ranklist, attentionHeader,
    } = this.state;
    const banners = list.map((item, index) => (
        <li key={item.index} data-index={index} onClick={this.changeIndex.bind(this, index)} className={activeIndex === item.index ? 'active' : ''}></li>
    ));
    const { index, user } = this.props;
    return (
      <div className='home_wrap'>
        <div className='header_banner'>
          <div className='main_banner'>
            <div className='main_left'>
              <div className='info_wrap'>
                <div className='avatar_info'>
                  <Avatar size={40} icon="user" />
                  <div className='user'>
                    <p className='nickname'>昵称很长很长昵称很长很长</p>
                    <p className='user_grade'>
                      <img className='grade' src="http://live-static.utoooo.com/common/level/icon_user_grade_1.png" alt="grade" />
                      <img className='grade' src="http://live-static.utoooo.com/common/level/icon_anchor_grade_1.png" alt="grade" />
                    </p>
                  </div>
                  <div className='focus_card'>关注</div>
                </div>
                <div className='room_info'>
                  <div className='viewer_num'>
                    <p>观众</p>
                    <p>15000</p>
                  </div>
                  <div className='fans_num'>
                    <p>粉丝</p>
                    <p>15000</p>
                  </div>
                  <div className='guard_num'>
                    <p>守护</p>
                    <p>8</p>
                  </div>
                </div>
              </div>
              <Liver ref='liver' loadPlayer={this.changeSrc} index={this.state.activeIndex} autoplay src={`http://livepull.utoooo.com/live/100005${this.state.activeIndex}.m3u8`}></Liver>
            </div>
            <div className='main_right'>
              <ul>
                {banners}
              </ul>
            </div>
          </div>

          <Tool></Tool>
        </div>
        <div className='index_content'>
          <div className='recommend'>
            <div className='index_left'>
              <div className='my_follows'>
              {
                user.status && index.attention.length > 0
                  ? <AnchorList data={index.room} header={attentionHeader}></AnchorList>
                  : null
              }

              </div>
              <div className='index_recommend'>
                <AnchorList data={index.room} header={recommendHeader}></AnchorList>
              </div>
            </div>
            <div className='index_right'>
              <div className='index_banner'>
                <CarouselBanner></CarouselBanner>
              </div>
              <div className='index_ranking'>
                <RankList
                  sort={'anchor'}
                  title={'主播榜'}
                  path={'index'}
                  data={ranklist}
                  handleData={anchorRank}
                  type={'anchor'}
                />
                <RankList
                  sort={'consume'}
                  title={'富豪榜'}
                  path={'index'}
                  data={ranklist}
                  handleData={tycoonRank}
                  type={'consume'}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  shouldComponentUpdate = (nextProps) => {
    if (nextProps.index.room === this.props.index.room) {
      return false;
    }
    return true;
  }
}

export default connect(
  ({ index, user }) => ({
    index, user,
  }),
  dispatch => ({
    rommList() {
      dispatch(setRoom());
    },
    myattention() {
      dispatch(attention());
    },
  }),
)(Index);
