import React, {
  Component,
} from 'react';
import { Icon } from 'antd';
import ChimeePlayer from 'chimee-player';
import chimeePluginControlbar from 'chimee-plugin-controlbar';
import contextmenu from 'chimee-plugin-contextmenu';
import chimeePluginCenterState from 'chimee-plugin-center-state';
import './index.scss';


export default class Liver extends Component {
  state = {
    player: {},
    display: '',
  }

  componentDidMount = () => {
    const { src } = this.props;
    // 自定义组件配置
    const popupPluginConfig = {
      name: 'mypopup',
      // 使用 el 设置插件的容器 HTMLTagName
      el: 'chimee-popup',
      className: 'tips',
      penetrate: true, // 是否穿透
      // 在插件创建生命周期，实现DOM结构和基本的交互行为
      create() {
        this.$dom.innerHTML = '<pp-body>进入直播间</pp-body>';
        this.$dom.style.display = 'none';
        // 为关闭按钮绑定关闭交互
        this.$dom.querySelector('pp-body').addEventListener('click', this.intoRoom);
      },
      methods: {
        // 为插件实现关闭功能
        close() {
          this.$dom.style.display = 'none';
          // 开始播放
          // this.$emit('play');
          // 通知全局popup关闭了，并通过参数将关闭的popup实例传递过去
          this.$emit('popupClose', this);
        },
        // 为插件实现开启功能
        open() {
          // 提升z-index为最高
          this.$bumpToTop();
          this.$dom.style.display = 'block';
          // 暂停播放
          // this.$emit('pause');
          // 通知全局popup打开了，并通过参数将关闭的popup实例传递过去
          this.$emit('popupOpen', this);
        },
        intoRoom(e) {
          console.log(e);
        },
      },
      events: {
        // 监听播放器播放事件，以实现播放时自动关闭popup
        error() {
          console.log('出现错误');
        },
        // // 监听播放器暂停事件，以实现暂停时自动打开popup
        // pause() {
        //   this.open();
        // },
        mouseover() {
          this.open();
        },
        mouseout() {
          this.close();
        },
      },
    };
    const popupPluginErrorConfig = {
      name: 'popup',
      // 使用 el 设置插件的容器 HTMLTagName
      el: 'error-popup',
      className: 'tips_error',
      penetrate: true, // 是否穿透
      // 在插件创建生命周期，实现DOM结构和基本的交互行为
      create() {
        this.$dom.innerHTML = "<pp-error class='red'>主播正在休息</pp-error><pp-error-tips>尝试 <span class='red refurbish'>刷新页面</span> 获取新内容</pp-error-tips>";
        this.$dom.style.display = 'none';
        // 为关闭按钮绑定关闭交互
        this.$dom.querySelector('.refurbish').addEventListener('click', this.refurbish);
      },
      destroy() {
        console.log('监听到了销毁');
      },
      methods: {
        // 为插件实现关闭功能
        refurbish() {
          window.location.reload();
        },
      },
      events: {
        // 监听播放器播放事件，以实现播放时自动关闭popup
        error() {
          console.log('出现错误');
          this.$dom.style.display = 'block';
        },
        player() {
          console.log('正常播放');
          this.$dom.style.display = 'none';
        },
        load() {
          this.$dom.style.display = 'none';
        },
      },
    };
    // 注册（初始化）自定义组件
    ChimeePlayer.install(popupPluginConfig); // 状态
    ChimeePlayer.install(popupPluginErrorConfig); // 状态
    // ChimeePlayer.install(popupPluginPosterConfig); // 左下角广告
    const player = new ChimeePlayer({
      src,
      wrapper: '#video', // video dom容器
      box: 'hls',
      isLive: true,
      autoplay: true,
      controls: true,
      noDefaultContextMenu: 'wrapper',
      plugin: [
        {
          name: chimeePluginControlbar.name,
          hoverColor: '#EF0716',
          className: 'test',
        },
        {
          name: contextmenu.name,
          baseMenus: [{
            text: 'By Pluto',
            disable: true,
          }, {
            text: 'Pluto 最帅',
            disable: true,
          }],
        },
        {
          name: chimeePluginCenterState.name,
          errorTips: '错误提示',
        },
        { name: popupPluginConfig.name },
        { name: popupPluginErrorConfig.name },
      ],
    });
    window.player = player;

    // 注册监听事件
    player.on('mouseover', () => {}, { target: 'container' });
    player.on('mouseout', () => {}, { target: 'container' });
    player.on('error', (err) => { console.log(err.data); });
    player.on('play', () => { console.log('正在播放'); });
    player.on('load', () => { console.log('正在加载'); });
    console.log(player);
  }

  loadPlayer = (src) => {
    window.player.load(src);
  }

  destory = () => {
    window.player.destroyed = true;
  }

  componentWillUnmount = () => {
    // 应该要在组件销毁的时候把live也销毁
    this.destory(); // 好像不管用 =_=
  }

  handleClose = () => {
    this.setState({
      display: 'none',
    });
  }

  render() {
    console.log(this.props);
    return (<div className = 'video-page' >
      <div id = 'video' >
        <div className={`poster ${this.state.display}`}>
          <Icon onClick={this.handleClose} className='close' style={{ fontSize: '18px', color: '#878585' }} type="close-circle" />
          <img src="" alt="code" />
          <p>下载豪门直播APP</p>
          <p>立即与主播1v1私聊！</p>
        </div>
      </div></div>
    );
  }
}
