import React, { Component } from 'react';
import { Carousel } from 'antd';
import './index.scss';
import { connect } from 'react-redux';
import { setbanner } from '../../view/actions/index/index';

class CarouselBanner extends Component {
  componentDidMount = () => {
    this.props.bannerList();
  }

  render() {
    const { index } = this.props;
    const list = index.banner.map((item, i) => (
        <div key={i}><img src={item.pic} alt="img" /></div>
    ));

    return (
      <div className='banner'>
        <Carousel autoplay>
          {list}
        </Carousel>
      </div>
    );
  }

  shouldComponentUpdate = (nextProps) => {
    if (nextProps.index.banner === this.props.index.banner) {
      return false;
    }
    return true;
  }
}
export default connect(
  ({ index }) => ({
    index,
  }),
  dispatch => ({
    bannerList(url) {
      dispatch(setbanner(url));
    },
  }),
)(CarouselBanner);
