import { INDEX_BANNER, INDEX_ROOM, INDEX_ATTENTION } from '../../constants/index/index';
import { roomList, banner } from '../../../asset/js/api';

export const getBanner = data => ({
  type: INDEX_BANNER,
  data,

});

export const getRoom = data => ({
  type: INDEX_ROOM,
  data,
});

export const getattention = data => ({
  type: INDEX_ATTENTION,
  data,
});

// 获取我的关注
export function attention() {
  return (dispatch) => {
    const url = {
      page: 0,
      status: 10,
      limit: 5,
    };
    roomList(url).then((data) => {
      dispatch(getattention(data));
    }).catch((err) => {
      console.log(err);
    });
  };
}
// 获取热门推荐
export function setRoom() {
  return (dispatch) => {
    const url = {
      page: 0,
      status: 1,
    };
    roomList(url).then((data) => {
      dispatch(getRoom(data));
    }).catch((err) => {
      console.log(err);
    });
  };
}

// 获取 banner 图
export function setbanner() {
  return (dispatch) => {
    banner().then((data) => {
      dispatch(getBanner(data));
    }).catch((err) => {
      console.log(err);
    });
  };
}
